* {
  font-family: Arial, sans-serif;
}
body {
  margin: 0px;
}
a {
  text-decoration: none;
  color: inherit;
}
td > a {
  height: inherit;
  display: inherit;
  width: inherit;
  vertical-align: inherit;
  cursor: default;
}
th span {
  font-size: 12px;
  display: block;
  font-weight: 300;
  color: #dddddd;
}

table {
  border-collapse: collapse;
  text-align: center;
}

th {
  background-color: #212121;
  color: #fff;
  border-style: solid;
  border-width: 1px;
  border-color: #212121;
  margin: 0;
}

.hour{
  padding: 10px 0px;
}

.date {
  width: 40px;
}
.date a {
  cursor: pointer;
}

.date span {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
  display: block;
  color: #fff;
  text-transform: uppercase;
}

td {
  width: 140px;
  height: 140px;
  border-style: solid;
  border-width: 3px;
  border-color: #f5f5f5;
  padding: 0;
}

.timetable:nth-child(even) .fill {
  background-color: #fff;
}

.timetable:nth-child(odd) .fill {
  background-color: #fff;
}

.timetable:nth-child(even) .date {
  background-color: #f5f5f5;
}

.timetable:nth-child(odd) .date {
  background-color: #f5f5f5;
}

.timetable:hover .fill {
  background-color: #f5f5f5;
}

.full {
  font-weight: 700;
  font-size: 22px;
}

.full span {
  display: block;
  font-weight: 300;
  font-size: 14px;
  color: #222;
}
.full>div {
  position: relative;
  font-weight: 700;
  height: 100%;
}
.full>div>div {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 100%;
}
.split>div {
  font-weight: 700;
  width: 100%;
  position: relative;
 /* font-size: 14px; */
}

.split>div>div {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 100%;
}

.split>div span {
  margin-left: 2px;
  display: inline-block;
  font-weight: 300;
  font-size: 10px;
}

.split>div {
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #eeeeee;
}

.split>div:last-of-type {
  border-bottom-style: none;
}

.class {
  border-style: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 25px;
  background-color: #ddd;
  color: #000;
  height: 60px;
}

.odpada {
  background-color: rgba(231, 76, 60, 0.8);
  color: #fff;
}

