.spoji, .zmena, .supluje, .presun, .navic, .vymena {
  color: #fff;
  background-color: rgba(243, 156, 18, 0.8);
}
.odpada span, .spoji span, .zmena span, .presun span, .navic span, .vymena span, .supluje span {
  color: #fff;
}

.button-today, .button-next {
  background: #dddddd;
  height: 40px;
  padding: 0px 20px;
  text-align: center;
  text-transform: uppercase;
  border: 1px #cecece solid;
}

.button-today {
  position: absolute;
  left: 10px;
  top: 10px;
}

.button-next {
  position: absolute;
  right: 10px;
  top: 10px;
}

.suplHead {
  width: 100%;
  border-style: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 25px;
  background-color: #ddd;
  color: #000;
  height: 60px;
  line-height: 60px;
  text-align: center;
  vertical-align: middle;
}

.footer-basic-centered{
  background-color: #292c2f;
  -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
          box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  text-align: center;
  font: normal 18px sans-serif;

  padding: 45px;
  margin-top: 300px;
}

.footer-basic-centered .footer-company-motto{
  color: #dadde0;
  font-size: 24px;
  margin: 0;
}

.footer-basic-centered .footer-company-name{
  color:  #8f9296;
  font-size: 14px;
  margin: 0;
}

@media (max-width: 600px) {

  .footer-basic-centered{
    padding: 35px;
  }

  .footer-basic-centered .footer-company-motto{
    font-size: 18px;
  }

  .footer-basic-centered .footer-company-name{
    font-size: 12px;
  }
}
@media screen and (min-width: 3800px) {
  .tableK {
    float: left;
    height: 100%;
    width: 33%;
  }
  .tableK:nth-child(2n+ 1) {
    margin-left: 19px;
    margin-right: 19px;
  }
}


